<template>
    <div>
        <StandardHeader :search="search"></StandardHeader>
        <div>
            <img src="assets/img/coming-soon.jpg" id="coming-soon"/>
        </div>
    </div>
</template>

<script>
// Library components.
import { mapState } from 'vuex'
// Custom components.
import StandardHeader from './StandardHeader.vue'
import router from '../router'

export default {
    name: 'Home',
    components: {
        StandardHeader
    },
    data() {
        return {
            loading: false
        }
    },
    methods: {
        search() {
            router.push({ path: `/SearchResults` }) 
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    #coming-soon{
        margin: 60px;
        margin-left: auto;
        margin-right: auto;
        display: block;
    }
</style>



